html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
/* fira-sans-regular - latin_cyrillic */
@font-face {
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Fira Sans Regular'), local('FiraSans-Regular'), url('../fonts/fira-sans-v10-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/fira-sans-v10-latin_cyrillic-regular.woff') format('woff');
  /* Modern Browsers */
  font-display: swap;
}
/* fira-sans-500 - latin_cyrillic */
@font-face {
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 500;
  src: local('Fira Sans Medium'), local('FiraSans-Medium'), url('../fonts/fira-sans-v10-latin_cyrillic-500.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/fira-sans-v10-latin_cyrillic-500.woff') format('woff');
  /* Modern Browsers */
  font-display: swap;
}
/* fira-sans-700 - latin_cyrillic */
@font-face {
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 700;
  src: local('Fira Sans Medium'), local('FiraSans-Medium'), url('../fonts/fira-sans-v10-latin_cyrillic-700.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/fira-sans-v10-latin_cyrillic-700.woff') format('woff');
  /* Modern Browsers */
  font-display: swap;
}
body {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 108%;
  word-wrap: break-word;
  letter-spacing: normal;
  text-align: center;
  background-position: top center;
  background-repeat: no-repeat;
  background-color: #f5f7fa;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.sprite-cont {
  display: none;
}
.divs {
  min-height: 850px;
  padding-top: 84px;
}
.divs__center {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 956px;
  min-height: 766px;
  padding-left: 55px;
  padding-right: 55px;
  margin-left: auto;
  margin-right: auto;
}
.divs__schedule {
  margin-bottom: 25px;
}
.divs__panel {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 211px;
}
.slide-switch {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 768px;
  margin-bottom: -10px;
}
.slide-switch__dot {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  width: 28px;
  height: 28px;
  padding-bottom: 2px;
  margin-bottom: 60px;
  font-weight: 700;
  line-height: 21px;
  color: #0066ff;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid #dfe4ef;
  border-radius: 14px;
}
.slide-switch__dot--left {
  color: #333333;
  border-color: #0066ff;
}
.slide-switch__dot--active {
  border-color: #ff238d;
}
.slide-switch__dot .slide-switch__label {
  width: 120px;
  height: 25px;
  margin-bottom: 14px;
  margin-left: 6px;
  font-weight: 500;
  font-size: 13px;
}
.slide-switch__connector {
  width: 120px;
  height: 2px;
  margin-bottom: 60px;
  background-color: #dfe4ef;
}
.slide-switch__connector--left {
  height: 4px;
  background-color: #0066ff;
}
.slide-switch__connector--active {
  background: linear-gradient(90deg, #0066ff 0%, #ff238d 100%);
}
.schedule {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 764px;
  width: auto;
  min-height: 525px;
  padding-top: 57px;
  padding-bottom: 63px;
  padding-left: 100px;
  padding-right: 99px;
  background-color: #ffffff;
  border-radius: 6px;
  box-shadow: 0px 1px 4px rgba(0, 15, 38, 0.1);
}
.schedule__h3 {
  margin-top: 0;
  margin-bottom: 34px;
}
.schedule__range {
  margin-bottom: 21px;
}
.schedule__chart {
  align-self: center;
  margin-bottom: 20px;
}
.schedule__panel {
  align-self: flex-end;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 310px;
}
.h3 {
  font-weight: 500;
  font-size: 22px;
}
.range {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 306px;
  min-height: 37px;
}
.range__caption {
  width: 188px;
  text-align: left;
}
.range__body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 110px;
}
.range__time-field-wrap {
  margin-top: -3px;
}
.range__dash {
  margin-top: -1px;
  color: #8e9cbb;
}
.time-field-wrap {
  position: relative;
  width: 42px;
  height: 40px;
  overflow-x: hidden;
}
@-moz-document url-prefix() {
  .time-field-wrap {
    width: 37px;
  }
}
@-moz-document url-prefix() {
  .time-field-wrap {
    height: 31px;
  }
}
@-moz-document url-prefix() {
  .time-field-wrap {
    border-bottom: 1px solid #bfc9e0;
  }
}
.time-field-wrap ::after {
  content: '';
  position: absolute;
  bottom: 9px;
  left: 3px;
  width: 36px;
  height: 1px;
  background-color: #bfc9e0;
}
.time-field-wrap__time-field {
  /* Firefox */
  border: none;
  outline: none;
}
@-moz-document url-prefix() {
  .time-field-wrap__time-field {
    margin-left: -4px;
    letter-spacing: -1pt;
  }
}
.chart {
  display: flex;
  font-size: 14px;
  overflow: hidden;
  transition-property: width;
  transition-duration: 3s;
  transition-timing-function: easeInSine;
}
.chart__head-col {
  z-index: 1;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  padding-top: 29px;
  line-height: 220%;
}
.chart__table {
  margin-left: 0;
}
.table {
  flex-shrink: 0;
  width: 816px;
  min-height: 241px;
}
.table__head {
  display: flex;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  height: 30px;
}
.table__body {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}
.table__cell {
  width: 34px;
  height: 31px;
}
.table__cell--head {
  padding-left: 3px;
  padding-right: 3px;
  font-weight: inherit;
  text-align: left;
  word-wrap: normal;
}
.table__cell--head:nth-child(odd):not(:last-child) {
  border-right: 1px solid #dfe4ef;
}
.table__cell--head:nth-child(even) {
  position: relative;
}
.table__cell--head:nth-child(even)::after {
  content: "";
  position: absolute;
  bottom: 1px;
  right: 0;
  width: 1px;
  height: 9px;
  background-color: #dfe4ef;
}
.table__cell--data {
  border-style: solid;
  border-color: #ffffff;
  border-top-width: 0;
  border-bottom-width: 1px;
  border-left-width: 0;
  border-right-width: 1px;
  transition-property: background-color;
  transition-duration: 0.8s;
  transition-timing-function: easeInSine;
  cursor: pointer;
}
.head-col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-shrink: 0;
  width: 39px;
  height: 247px;
  background-color: #ffffff;
}
.head-col__cell {
  height: 31px;
}
.btn {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-width: 100px;
  max-width: 197px;
  font-weight: 500;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #d3d8df;
  border-radius: 50px;
  cursor: pointer;
}
.btn:hover {
  outline: none;
}
.btn--fill {
  min-height: 30px;
  font-size: 12px;
}
.btn--auto {
  padding-left: 9px;
  padding-right: 14px;
}
.btn--auto .btn__caption {
  max-width: 148px;
}
.btn--clear {
  padding-left: 10px;
  padding-right: 16px;
}
.btn--clear .btn__caption {
  max-width: 146px;
}
.btn--nav {
  min-height: 38px;
}
.btn--next {
  color: #ffffff;
  background-color: #0066ff;
  border: none;
}
.btn__ok-svg {
  margin-top: -3px;
  margin-right: 9px;
}
.btn__cancel-svg {
  margin-right: 11px;
}
.footer {
  min-height: 60px;
  padding-top: 20px;
  padding-bottom: 19px;
  font-size: 14px;
  border-top: 1px solid #dfe4ef;
}
.footer__center {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 956px;
  min-height: 21px;
  margin-left: auto;
  margin-right: auto;
}
.footer__col {
  min-height: inherit;
}
.footer__col--company-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 212px;
}
.footer__col--logo {
  width: 90px;
  padding-top: 1px;
}
.footer__col--copyrights {
  width: 95px;
  padding-top: 3px;
}
.footer__col--tech-support {
  width: 187px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: #0066ff;
}
.footer__tech-support-caption {
  width: 159px;
  min-height: inherit;
  padding-top: 3px;
}
